import {
  ValidatorFn,
  AbstractControl
} from '@angular/forms';

import { ObjectMaker } from '../util/object-maker';
import { RuleConfig } from '../models/config/rule-config';
import { FormProvider } from '../util/form-provider';
import {getConfigObject} from '../util/config-provider';
/**
 * @param {?} configModel
 * @param {?} entity
 * @return {?}
 */
export function ruleValidator(configModel: RuleConfig, entity: any): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } => {
    let /** @type {?} */ config = getConfigObject(configModel,control);
    if (FormProvider.ProcessRule(control, config)) {
      let /** @type {?} */ result = null;
      for (let /** @type {?} */ rule of config.customRules) {
        result = rule(entity);
        if (result)
          break;
      }
      if (result)
        return result;
    } return ObjectMaker.null();
  }
}
