import {
  ValidatorFn,
  AbstractControl
} from '@angular/forms';

import { ObjectMaker } from '../util/object-maker';
import { DefaultConfig } from '../models/config/default-config';
import { AnnotationTypes } from '../core/validator.static';
import { ValidatorValueChecker } from '../util/validator-value-checker';
import {getConfigObject} from '../util/config-provider';
/**
 * @param {?} configModel
 * @return {?}
 */
export function jsonValidator(configModel: DefaultConfig): ValidatorFn {
/**
 * @param {?} value
 * @return {?}
 */
function process(value) {
    var /** @type {?} */ result: boolean = false;
    try {
      var /** @type {?} */ json = JSON.parse(value);
      result = !!json && typeof json === 'object'
    }
    catch ( /** @type {?} */ex) {
      result = false;
    }
    return result;
  }

  return (control: AbstractControl): { [key: string]: any } => {
    let /** @type {?} */ config = getConfigObject(configModel,control);
    if (ValidatorValueChecker.pass(control, config)) {
      if (process(control.value))
        return ObjectMaker.toJson(AnnotationTypes.json, config, [control.value]);
    }
    return ObjectMaker.null();
  }
}
