import { AbstractControl } from '@angular/forms';
import { DateProvider } from './date-provider';
import { FormProvider } from './form-provider';
import { RegexValidator } from './regex-validator';
import { ObjectMaker } from './object-maker';
import { AnnotationTypes } from '../core/validator.static';
import {OPERATORS} from '../const/operators.const';
import {getConfigObject} from '../util/config-provider';
/**
 * @param {?} leftValue
 * @param {?} rightValue
 * @param {?} operator
 * @return {?}
 */
function runCondition(leftValue:Date,rightValue:Date,operator:string):boolean{
    let /** @type {?} */ result:boolean  = false;
    switch(operator){
        case OPERATORS.lessThan:
        case OPERATORS.greaterThan:
         result = leftValue > rightValue;
        break;
        case OPERATORS.lessThanEqualTo:
        case OPERATORS.greaterThanEqualTo:
         result = leftValue >= rightValue;
        break;
    }
    return result;
}
/**
 * @param {?} control
 * @param {?} config
 * @param {?} operationType
 * @return {?}
 */
export function dateChecker(control: AbstractControl,config:any,operationType:string): { [key: string]: any } {
    config = getConfigObject(config,control);
    var /** @type {?} */ dateProvider = new DateProvider();
      if (FormProvider.ProcessRule(control,config)) {
        if (RegexValidator.isNotBlank(control.value)) {
            if (dateProvider.isDate(control.value) || dateProvider.isValid(control.value)) {
                let /** @type {?} */ checkDate = dateProvider.getCompareDate(config,control);
                let /** @type {?} */ currentControlValue = dateProvider.getDate(control.value);
                let /** @type {?} */ isValid = operationType == AnnotationTypes.minDate ? runCondition(currentControlValue,checkDate,config.operator || OPERATORS.greaterThanEqualTo): runCondition(checkDate,currentControlValue,config.operator || OPERATORS.lessThanEqualTo)
                if (!isValid)
                    return ObjectMaker.toJson(operationType, config, [control.value])
            } else
                return ObjectMaker.toJson(operationType, config, [control.value])
        }
    }
    return ObjectMaker.null();
}
/**
 * @param {?} control
 * @param {?} config
 * @param {?} operationType
 * @return {?}
 */
export function validateDate(control: AbstractControl,config:any,operationType:string): { [key: string]: any } {
    config = getConfigObject(config,control);
    var /** @type {?} */ dateProvider = new DateProvider();
      if (FormProvider.ProcessRule(control,config)) {
        if (RegexValidator.isNotBlank(control.value)) {
            if (!dateProvider.isDate(control.value) && !dateProvider.isValid(control.value)) {
                return ObjectMaker.toJson(operationType, config, [control.value])
        }
    }
}
    return ObjectMaker.null();
}