import { InstanceContainer } from '../core/validator.interface';
import { defaultContainer } from '../core/defaultContainer';
import { clone, merge } from '../services/entity.service';
/**
 * @param {?} instanceFunc
 * @param {?=} entityObject
 * @return {?}
 */
export function instanceProvider(instanceFunc: any, entityObject?: any): InstanceContainer {
    let /** @type {?} */ instance: any = defaultContainer.get(instanceFunc);
    let /** @type {?} */ prototype: any = entityObject ? entityObject.__proto__ : getInstance(instanceFunc, []).__proto__;
    if (prototype.__proto__) {
        let /** @type {?} */ isLoop = false;
        do {
            isLoop = prototype.__proto__.constructor != Object;
            if (isLoop) {
                let /** @type {?} */ extendClassInstance: any = defaultContainer.get(prototype.__proto__.constructor);
                instance = merge(clone(instance), clone(extendClassInstance))
                prototype = prototype.__proto__;
            }
        } while (isLoop)

    }
    return instance;
}
/**
 * @param {?} model
 * @param {?} objectArguments
 * @return {?}
 */
export function getInstance(model: any, objectArguments: any[]) {
    let /** @type {?} */ classInstance = Object.create(model.prototype)
    try{
        model.apply(classInstance, objectArguments);
    }catch( /** @type {?} */ex){
        ///resolution of issue https://github.com/rxweb/rxweb/issues/188
        classInstance = Reflect.construct(model,objectArguments);
    }
    return classInstance;
}