
/**
 * @param {?} length
 * @param {?} checks
 * @return {?}
 */
export function checkLength(length: number, checks: number[]): boolean {
  let /** @type {?} */ isPassed = false;
  for (let /** @type {?} */ check of checks) {
    isPassed = (check == length)
    if (isPassed)
      break;
  }
  return isPassed;
}
